@import "../../assets/styles/base.scss";


.container {
  min-height: 118px;
  display: flex;
  width: 637px;
  padding: 16px 0 0 23px;
  border-bottom: 1px solid #E6ECF0;

  .avatar {
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  .userPanel {
    width: 550px;
    padding-left: 8px;

    .accountInfo {
      display: flex;
      align-items: center;

      .name {
        font-weight: 700;
      }

      .accountAndTime {
        margin-left: 8px;
      }
    }

    .accountAndTime,
    .reply,
    .replyPostUser {
      color: var(--secondary-color);
      font-size: 14px;
      font-weight: 400;
    }

    .replyUser {
      display: flex;
      align-items: center;
      margin-top: 8px;
      margin-bottom: 8px;

      .replyPostUser {
        color: var(--active-color);
        margin-left: 5px;
      }
    }

    .tweetContainer,
    .replyContainer {
      width: 100%;
      overflow-wrap: break-word;
      color: var(--dark-100);
      font-weight: 400;
      font-size: 16px;
      min-height: 55px;
    }

    .tweetContainer {
      margin: 12px 0 9px;
    }

    .replyContainer {
      margin: 12px 0 16px;
    }
  }

  .iconEffect {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    height: 14px;

    .replyEffect,
    .likeEffect {
      display: flex;
      align-items: center;
    }

    .replyIcon,
    .likeIcon {
      width: 16px;
      margin-right: 8px;
      &.cursor-point {
          cursor: pointer;
        }
    }

    .likeEffect {
      margin-left: 40px;
    }

    h6 {
      font-family: var(--numeric-font-family);
      font-weight: 600;
      font-size: 14px;
      color: #6C757D;
    }
  }

  //新增Count們的margin
  .replyCount,
  .likeCount {
    margin-left: 5px;
  }
}